import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { Observer } from "rxjs/Observer";
import { isNullOrUndefined } from "util";
import { tap } from "rxjs/operators/tap";
import * as excel from "xlsx";


@Injectable()
export class ExcelController {
	public reader: FileReader = undefined;
	public dataUrl: string = undefined;

	/**
	 * Load data from file excel
	 * @param {file} File
	 * */
	public loadFromFile(file: File): Observable<excel.WorkBook> {
		return Observable.create((obs: Observer<any>) => {

			// check file is exit
			if(isNullOrUndefined(file)){
				obs.error("Bạn chưa nhập tập tin.")
			}

			this.reader = new FileReader();
			this.reader.readAsDataURL(file);
			this.reader.onerror = (e:any) => { obs.error(e) }
			this.reader.onload = (e: any) => {
				let status = this.reader.readyState;
				if( status === 2 ) {
					let data = this.reader.result;
					this.dataUrl = data;
					
					data = data.split(",")[1];
					let wb = excel.read(data, {type: 'base64'});
					obs.next(wb);
					obs.complete();
				}
			}
		})
	}

	/**	 
	 * Return json object
	 * @param {string} name - The sheet name
	 * @returns {header: object, data: object[]}
	 * */
	public getSheetByName(wb: excel.WorkBook, name: string, begin?: number, end?: number): Observable<any> {
		let ws = wb.Sheets[name];

		if(isNullOrUndefined(ws)) {
			return Observable.throw(`Không có sheet "${name}".`);
		}

		let data = excel.utils.sheet_to_json(ws);
		
		if(data.length <= 1) {
			return Observable.throw(`Sheet "${name}" không có dữ liệu.`);
		}

		// convert row0 of data => field +  title
		let row0 = data.shift();
		let ids: string[] = Object.keys(row0).filter(key => key !== "__EMPTY");
		let header = ids.map((id, index) => {return {field: id, title: row0[id]}});

		// copy data from `begin` to `end`
		begin = begin ? begin - 1 : 0;
		end = end ? end - 1 : data.length - 1;
		data = data.slice(begin, end + 1);

		return Observable.of({header, data, begin: begin + 1, end: end + 1});
	}

	/**
	 * Returns the sheet at `index`;
	 * @param {excel.WorkBook} wb
	 * @param {number} index - The zero based index of the reQuired sheet
	 * @returns {excel.WorkSheet | null}
	 * */
	public getSheetByIndex(wb: excel.WorkBook, index: number): excel.WorkSheet {
		let names = wb ? wb.SheetNames : [];
		let name = names.length >= index + 1 ? names[index] : null;
		return name ? wb.Sheets[name] : null;
	}
}


