import { Injectable } from "@angular/core";
import { AppConfig } from "./config.ctrl";
import { Observable } from "rxjs";

export interface IDataSend {
	action: string,
	params?: {[p: string]: any},
	body?: {[p: string]: any},
}

@Injectable()
export class KPIController {
	
	constructor(private cfig: AppConfig) {}
	
	private send(method: string, data: IDataSend){
		let body = JSON.stringify(data.body || {});
		let params = Object.assign({}, data.params, {action: data.action});
		const url = this.cfig.get().server_api;
		return this.cfig.http.request(method, url, {params, body}).switchMap(resp => {
			if(resp['status'] === 'error') return Observable.throw(resp);
			else return Observable.of(resp['result']);
		});
	}
	
	createFile(email: string, dataUrl: string, fileName: string) {
		let data = dataUrl.replace(/^.*,/, '');
		let contentType = dataUrl.match(/^.*(?=;)/)[0];
		return this.send("POST", {
			action: "createFile",
			params: {email},
			body: {data: data, mimeType: contentType, fileName}
		})
	}
	
	sendRow(email: string, data: {[id: string]: any}) {
		return this.send("POST", {
			action: "appendRow",
			params: {email},
			body: {item: data}
		});
	}
	
	getFiles(email: string) {
		return this.send("GET", {
			action: "getAllFile",
			params: {email}
		})
	}

}


