import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { isNullOrUndefined } from 'util';
import { ExcelController } from '../../providers/excel.ctrl';
import { tap } from 'rxjs/operators';

@Component({
	selector: 't2t-select-excel',
	templateUrl: './dialog.component.html',
	styleUrls: ['./dialog.component.css']
})
export class DialogComponent implements OnInit {
	@Output() load = new EventEmitter<any>();
	@Output() close = new EventEmitter<any>();

	acceptFile: string = "application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
	fileImport: File = undefined;	
	wb: any = undefined;
	isLoading: boolean = false;
	form: FormGroup;

	constructor(
	private fb: FormBuilder,
	private ctrl: ExcelController) { }

	ngOnInit() {
		this.form = this.fb.group({
			name: ['data', Validators.required],
			begin: [1],
			end: []
		});
	}

	/**
	 * Returns label file import 
	 * */
	get lblFile(): string {
		if(!isNullOrUndefined(this.fileImport)) 
			return this.fileImport.name;
		else 
			return "Chọn file dữ liệu excel xlsx hoặc xls.";
	}
	
	/**
	 * Returns sheet names
	 * */
	 get sheetNames(): string[] {
		 return this.wb ? this.wb.SheetNames : [];
	 }

	/**
	 * Check file on select
	 * */
	get fileNotExist(): boolean {
		return isNullOrUndefined(this.fileImport);
	}

	/**
	 * Change select file
	 * @param {EventChange} evt
	 * */
	onChangeFile(evt: any) {
		let target: DataTransfer = evt.target;
		if(target.files.length > 0 ) {
			this.fileImport = target.files.item(0);
			this.isLoading = true;
			this.ctrl.loadFromFile(this.fileImport)
			.pipe(tap(() => this.isLoading = false))
			.subscribe(wb => this.wb = wb);
		}
	}

	/**
	 * On Close #thoát
	 * */
	onClose() { this.close.emit() }
	
	/**
	 * On Stop #Dừng lại
	 * */
	onstop() { 
		this.ctrl.reader.abort() 
	}

	/**
	 * Load excel data from file
	 * */
	onLoadExcel() {
		this.isLoading = true;
		let sheetName = this.form.value['name'];
		let begin: number = this.form.value['begin'];
		let end: number = this.form.value['end'];
		this.ctrl.getSheetByName(this.wb, sheetName, begin, end)
			.pipe(tap(() => this.isLoading = false))
			.subscribe(data => { 
				this.load.emit({
					file: this.fileImport,
					dataUrl: this.ctrl.dataUrl,
					excel: data
				})
			})
	}

}



