import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule, APP_INITIALIZER } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HttpClientModule, HTTP_INTERCEPTORS } from "@angular/common/http";
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';

import { GridModule } from '@progress/kendo-angular-grid';
import { DialogsModule } from '@progress/kendo-angular-dialog';
import { ButtonsModule } from '@progress/kendo-angular-buttons';
import { DropDownsModule } from '@progress/kendo-angular-dropdowns';

import { ToastrModule } from "ngx-toastr";
import { AppComponent } from './app.component';
import { HomeComponent } from './comps/home/home.component';
import { DialogComponent } from './comps/dialog/dialog.component';
import { LogComponent } from './comps/log/log.component';
import { AppConfig } from './providers/config.ctrl';
import { ExcelController } from './providers/excel.ctrl';
import { KPIController } from './providers/kpi.ctrl';
import { ToastController } from './providers/toast.ctrl';
import { Interceptor } from './providers/intercep.ctrl';

export function LOAD_CFIG(cfig: AppConfig) {
	return () => cfig.load().catch(msg => console.error(msg));
}  

export const ROUTING: Routes = [
	{path: "**", component: HomeComponent}
];

@NgModule({
	declarations: [
		AppComponent,
		HomeComponent,
		DialogComponent,
		LogComponent
	],
	imports: [
		BrowserModule,
		BrowserAnimationsModule, 
		CommonModule,
		HttpClientModule,
		ReactiveFormsModule,
		RouterModule.forRoot(ROUTING, {useHash: true}),
		
		ToastrModule.forRoot({}),

		GridModule,
		DialogsModule,
		ButtonsModule,
		DropDownsModule,
	],
	providers: [
		{provide: HTTP_INTERCEPTORS, useClass: Interceptor, multi: true},
		{provide: AppConfig, useClass: AppConfig},
		{provide: APP_INITIALIZER, useFactory: LOAD_CFIG, deps: [AppConfig], multi: true},
		{provide: ExcelController, useClass: ExcelController},
		{provide: KPIController, useClass: KPIController},
		{provide: ToastController, useClass: ToastController},

	],
	bootstrap: [AppComponent]
})
export class AppModule { }



