import { Component, OnInit, ViewChild, ElementRef} from '@angular/core';
import { KPIController } from '../../providers/kpi.ctrl';
import { ToastController } from '../../providers/toast.ctrl';
import { Observable } from 'rxjs';
import { tap, take } from 'rxjs/operators';
import "rxjs/add/observable/interval";

@Component({
	selector: 'app-home',
	templateUrl: './home.component.html',
	styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
	columns: object[] = [];
	data: any[] = [];
	dataUrl: string = undefined;
	fileName: string = undefined;
	showSelectFile: boolean = false;
	isSendKPI: boolean = false;
	
	@ViewChild("email") emailRef: ElementRef;
	@ViewChild("selectFile") selectFile: any;

	constructor(
		private toast: ToastController,
		private kpi: KPIController) {}

	ngOnInit(): void {}

	/**
	 * On Close dialog select file
	 * */
	onCloseDialog() {
		this.showSelectFile = false;
	}

	/**
	 * On select file import
	 * */
	onSelectFile() {
		this.showSelectFile = true;
	}

	/**
	 * On Load data from file excel
	 * */
	async loadFromExcel(evt) {		
		//this.selectFile.isLoading = true;
		this.fileName = `${Date.now()}.xlsx`;
		this.dataUrl = evt.dataUrl;
		this.columns = await evt.excel.header;
		this.data = await evt.excel.data;
		//this.selectFile.isLoading = false;
		
		this.toast.success(`Đã lấy dữ liệu từ file excel.`);
		await this.onCloseDialog();
		console.log(`Ten file ==> ${this.fileName}`)
	}

	/**
	 * Send data to google sheet
	 * */
	 sendData() {

		// check e-mail
		let email = this.emailRef.nativeElement.value;
		if(email.trim().length === 0) {
			this.toast.error("Bạn chưa nhập e-mail.");
			return;
		}
		
		// upload file import to google drive
		this.isSendKPI = true;
		this.kpi.createFile(email, this.dataUrl, this.fileName).subscribe(resp => {
			console.log(`Đã upload file xong => ${JSON.stringify(resp)}`);
			this.toast.success(`Đã chuẩn bị xong.`);

			// send all data to server
			let num = -1;
			let exec = Observable.interval(2000).subscribe(async (index) => {
				num++;

				if(this.data.length === num +1) {
					exec.unsubscribe();
					this.isSendKPI = false;
				}

				let item = this.data[num];
				item['status'] = 'send';
				await this.kpi.sendRow(email, item).subscribe(
					resp => { item['status'] = 'success'; console.log(resp) },
					err => { item['status'] = 'error'; console.warn(err) }
				);
			});
		})
	}
}



